.App {
  text-align: center;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}



#player1 {
  max-width: 600px;
}



#player2 {
  width:160px;
}

/* This is a hack. The player has internally set these values. */
.bitmovinplayer-container {
  min-height: 90px;
  min-width: 160px;
}

/*
  Bitmovin CSS elements are documented here:; 
  https://developer.bitmovin.com/hc/en-us/articles/115001507873-Player-UI-CSS-Class-Reference
*/
.bmpui-ui-watermark {
  display: none;
}
.bmpui-layout-max-width-400 .bmpui-ui-subtitle-label {
  display: none;
}

#player2 .bmpui-ui-watermark,
#player2 .bmpui-ui-titlebar,
#player2 .bmpui-ui-subtitle-label,
#player2 .bmpui-ui-seekbar,
#player2 .bmpui-ui-container.bmpui-controlbar-top,
#player2 .bmpui-ui-settingstogglebutton,
#player2 .bmpui-ui-fullscreentogglebutton {
  display: none;
}

#player2 .bmpui-controlbar-bottom {
  display: none;
}

#player2 .bmpui-ui-hugeplaybacktogglebutton {
  /* this doesn't work: all controls get hidden. */
  /* display: none; */
}




